
@font-face {
  font-family: tawbah100;
  src: url(./assets/fonts/tawbah100.ttf);
}

@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,500;0,600;1,300&display=swap');
* {
  box-sizing: border-box;
}

body {
  font-family: 'Raleway', sans-serif;
  margin: 0;
  padding: 0;
}

header {
  background-color: #f2f2f2;
  border-bottom:0.5px solid #c7c7c7;
  padding: 20px;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav a {
  color: #333;
  text-decoration: none;
  font-size: 16px;
}

nav a:hover {
  text-decoration: underline;
}

.posts {
  max-width: 1300px;
  margin: 0 auto; /* centers the posts horizontally */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 20px;
  padding: 0 20px;
}

.post {
  background-color: #f2f2f2;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 20px;
  padding: 20px;
}


.post-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.post-title {
  font-size: 24px;
  margin: 0;
  margin-bottom: 10px;
}

.post-image {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
}


.post-author {
  font-size: 16px;
  color: #888;
  margin: 0;
  margin-bottom: 10px;
}

.post-summary {
  font-size: 18px;
  margin: 0;
  margin-bottom: 20px;
}

.post-readmore {
  display: block;
  text-align: right;
  font-size: 16px;
  color: #333;
  text-decoration: none;
}

.post-readmore:hover {
  text-decoration: underline;
}


/* Slider */
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40vh;
}

.text-wrapper {
  text-align: center;
  padding: 10px;
}

.centered-text {
  text-align: center;
  margin: 0;
  font-family: tawbah100;
  font-size: large;
  max-width: 600px;
  opacity: 0.8;

}


.centered-text-2 {
  text-align: center;
  margin: 5;
  max-width: 600px;
  text-transform: capitalize;
  font-weight: 500;
  opacity: 0.8;
}

@media (min-width: 768px) {
  .post {
    width: calc(50% - 10px);
  }
}
@media (max-width: 768px) {
  .centered-text {
    font-size: small  ;
  }
  .centered-text-2 {
    font-size: x-small;
  }
}